import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { TransactionGroupMaintenanceServiceService } from '../transaction-group-maintenance-service.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-user-gl-ac-code-maint-summary',
  templateUrl: './user-gl-ac-code-maint-summary.component.html',
  styleUrls: ['./user-gl-ac-code-maint-summary.component.css']
})
export class UserGlAcCodeMaintSummaryComponent implements OnInit {
  UserId: any;
  userData: any;
  trndata: any;
  dataSource: any;
  trndata1: any;
  dataSource1: any;
  res: boolean;
  result : any;
  enabled:boolean=true;
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  displayedColumns1: string[] = ['userId', 'permission', 'trnCode', 'trnDesc', 'action'];
  displayedColumns: string[] = ['userId', 'permission', 'trnCode', 'trnDesc', 'action'];
  constructor(private ref:ChangeDetectorRef,
    private transactionGroupService: TransactionGroupMaintenanceServiceService) { }

  ngOnInit() {
    this.getUserIdOnNgOnInit();
    this.enabled=true;
    this.getUserId();
  }
  // added by gayathri issueid=0000652 on 18-08-2020 
  getUserIdOnNgOnInit(){
    this.transactionGroupService.getUserName().subscribe(userData=>{
      this.userData=userData;
     
      console.log(this.userData);
    });

  }
  //added by Fayaz 04/08/2020
  keyPress(event: any) {
    console.log(event.target.value);
    console.log(event);
  
    console.log(this.userData);
    this.result = this.userData.filter(s => s.includes(event.target.value));
    console.log(this.result);
    this.userData=this.result;
    this.ref.markForCheck();
    if(event.target.value=='')
    {
    this.getUserId();
    }  
  }
  //
  getUser(value)
{
    console.log(value);
    if(value !=this.UserId){
      console.log(" 2nd If")
      this.enabled = true; 
    }else{
      console.log(" 2nd else")
      this.enabled = false; 
      
    }
}
//added by gayathri on 24-08-2020
  onKey(event) {

    // console.log(value+"------------",data);
     event.target.value = event.target.value.trim();
     this.UserId = event.target.value;
     console.log("Value : ", this.UserId);
     if(this.UserId!=null){
       for (var value of this.userData) {
           console.log(value+"------------",this.userData);
           if(value !=this.UserId){
                        console.log(" 2nd If")
                        this.enabled = true; 
                      }else{
                        console.log(" 2nd else")
                        this.enabled = false; 
                        break;
                      }
                    }
                  }else{
                  console.log("data else : ",this.userData)
                 }
     }
   //added by gayathri issueid=0000652 on 24-08-2020
   getUser2(value)
   {
       console.log(value);
       if(value !=this.UserId){
         console.log(" 2nd If")
         this.enabled = true; 
       }else{
         console.log(" 2nd else")
         this.enabled = false; 
         
       }
   }
   onKey1(event) {

    // console.log(value+"------------",data);
     event.target.value = event.target.value.trim();
     this.UserId = event.target.value;
     console.log("Value : ", this.UserId);
     if(this.UserId!=null){
       for (var value of this.userData) {
           console.log(value+"------------",this.userData);
           if(value !=this.UserId){
                        console.log(" 2nd If")
                        this.enabled = true; 
                      }else{
                        console.log(" 2nd else")
                        this.enabled = false; 
                        break;
                      }
                    }
                  }else{
                  console.log("data else : ",this.userData)
                 }
     }
  getUserId() {
    this.transactionGroupService.getUserName().subscribe(userData => {
      this.userData = userData;
      console.log(this.userData);
    });
  }
  getGlTrndata() {
    this.transactionGroupService.getUserIdGlData(this.UserId).subscribe(trndata => {
      this.trndata = trndata;
      console.log(this.trndata);
      if (this.trndata.length !== 0) {
        this.dataSource = new MatTableDataSource(this.trndata);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      } else {
        Swal.fire('No transaction code mapped for this user.')
      }
    })
  }
  getTrndata() {
    this.transactionGroupService.getUserIdAccData(this.UserId).subscribe(trndata => {
      this.trndata1 = trndata;
      console.log(this.trndata);
      if (this.trndata1.length !== 0) {
        this.dataSource1 = new MatTableDataSource(this.trndata1);
        this.dataSource1.paginator = this.paginator;
        this.dataSource1.sort = this.sort;
      } else {
        Swal.fire('No transaction code mapped for this user.')
      }
    })
  }
  // getTransactionData()
  // {
  //   console.log("this is data from summary")
  //   this.transactionGroupService.gettingsummaryDataOfTransactionGroupMaintenance().subscribe(backendResp=>{
  //     console.log(backendResp);

  //   })
  deleteData(row) {
    console.log(row);
    //write a backend call.

    this.transactionGroupService.deleteGlDataUser(row, this.UserId).subscribe(res => {
      this.res = res;
      if (this.res) {
        Swal.fire('Transaction Mapping deleted successfully.')
        this.dataSource = '';
      } else {
        Swal.fire('Unable to delete');
      }
    })
  }
  deleteData1(row) {
    console.log(row);
    //write a backend call.

    this.transactionGroupService.deleteAcDataUser(row, this.UserId).subscribe(res => {
      this.res = res;
      if (this.res) {
        Swal.fire('Transaction Mapping deleted successfully.')
        this.dataSource = '';
      } else {
        Swal.fire('Unable to delete');
      }
    })
  }
}
